  --text-dark-alfa: #ccc0c0f3;
  --container-bgc: var(--color-light);
  --switcher-bgc: var(--color-dark);
  --switcher-text: var(--color-dark);
  --article-bgc: var(--color-light);
  --article-text: var(--text-light);
  --feedback-bgc: var(--color-light-cream);
  --feedback-text: var(--color-dark-alfa);
  --feedback-author: var(--color-accent-violet);
  --contact-container: var(--color-accent-gold);
  --nav-scroll-observer: var(--color-dark);
  --heading-scroll-observer: var(--color-dark);
  --button-icon: var(--icon-moon);
  --button-icon-filter: var(--icon-moon-filter);
  --button-background: var(--color-dark);
}

@media (prefers-color-scheme: dark) {
  :root {
    --color-mode: "dark";
  }

  :root:not([data-user-color-scheme]) {
    --container-bgc: var(--color-dark);
    --switcher-bgc: var(--color-light);
    --switcher-text: var(--color-light);
    --article-bgc: var(--color-dark);
    --article-text: var(--text-dark-alfa);
    --feedback-bgc: var(--color-dark-bright);
    --feedback-text: var(--color-light-alfa);
    --feedback-author: var(--color-accent-gold);
    --contact-container: var(--color-light-alfa);
    --nav-scroll-observer: var(--color-light-alfa);
    --heading-scroll-observer: var(--color-dark);
    --button-icon: var(--icon-sun);
    --button-icon-filter: var(--icon-sun-filter);
    --button-background: var(--color-dark);
  }
}

[data-user-color-scheme="dark"] {
  --container-bgc: var(--color-dark);
  --switcher-bgc: var(--color-light-alfa);
  --switcher-text: var(--color-light-alfa);
  --article-bgc: var(--color-dark);
  --article-text: var(--text-dark-alfa);
  --feedback-bgc: var(--color-dark-bright);
  --feedback-text: var(--color-light-alfa);
  --feedback-author: var(--color-accent-gold);
  --contact-container: var(--color-light-alfa);
  --nav-scroll-observer: var(--color-light-alfa);
  --heading-scroll-observer: var(--color-dark);
  --button-icon: var(--icon-sun);
  --button-icon-filter: var(--icon-sun-filter);
  --button-background: var(--color-dark);
}

html {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*, :before, :after {
  box-sizing: inherit;
}

.body {
  background-color: var(--container-bgc);
  font-family: Josefin Sans, Arial, sans-serif;
  transition: background-color .3s linear;
}

.sr-only {
  clip: rect(0, 0, 0, 0);
  height: 1px;
  white-space: nowrap;
  width: 1px;
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.hero-container {
  min-height: 100vh;
  background-image: url("min-sr1.dba27703.jpg");
  background-position: 100%;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
}

.hero-container #bg-dots {
  z-index: 4;
  position: absolute;
  inset: 0;
}

.hero-container:after {
  content: "";
  opacity: .4;
  background-color: #1d2525e8;
  position: absolute;
  inset: 0;
}

header {
  z-index: 999;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
}

.navigation {
  height: 10vh;
  background-color: #f5f5f5;
  justify-content: space-between;
  align-items: center;
  transition: background .2s ease-in;
  display: flex;
}

.navigation__container {
  letter-spacing: 3px;
  justify-content: center;
  align-items: center;
  display: flex;
}

.navigation__container-content {
  transition: color .2s ease-in;
  display: flex;
  position: relative;
}

.navigation__container-content-heading {
  width: 200px;
  text-transform: uppercase;
  color: #302b2bf5;
  margin-bottom: -10px;
  padding-left: 14px;
  font-size: 18px;
}

@media (max-width: 576px) {
  .navigation__container-content-heading {
    font-size: 14px;
  }
}

.navigation__container-content:after {
  content: "guitarist";
  color: #bf9660;
  letter-spacing: 4px;
  font-family: Oleo Script Swash Caps, cursive;
  font-size: 15px;
  position: absolute;
  top: 20px;
  left: 200px;
}

@media (max-width: 576px) {
  .navigation__container-content:after {
    font-size: 10px;
    top: 20px;
    left: 165px;
  }
}

.navigation__hamburgerButton {
  width: 45px;
  height: 45px;
  cursor: pointer;
  background-color: #0000;
  border: 0;
  justify-content: center;
  margin: 10px;
  transition: background .4s;
  display: flex;
  position: relative;
}

.navigation__hamburgerButton:focus {
  outline: 2px dotted #bf9660;
}

.navigation__firstLine, .navigation__firstLine:before, .navigation__firstLine:after {
  width: 28px;
  height: 3px;
  transition: transform .15 ease;
  background-color: #bf9660;
  border-radius: 4px;
  position: absolute;
  bottom: 12px;
}

@media (max-width: 576px) {
  .navigation__firstLine, .navigation__firstLine:before, .navigation__firstLine:after {
    width: 25px;
  }
}

.navigation__firstLine {
  transition-duration: .13s;
  transition-delay: .13s;
  transition-timing-function: cubic-bezier(.55, .055, .675, .19);
}

.navigation__firstLine:before {
  content: "";
  transition: top .12s cubic-bezier(.33333, .66667, .66667, 1) .2s, transform .13s cubic-bezier(.55, .055, .675, .19);
  display: block;
  top: -10px;
  left: 0;
}

.navigation__firstLine:after {
  content: "";
  transition: top .2s cubic-bezier(.33333, .66667, .66667, 1) .2s, opacity .1s linear;
  display: block;
  top: -20px;
  left: 0;
}

.navigation__list {
  width: 60%;
  height: 100vh;
  z-index: 1000;
  background-color: #302b2bf5;
  flex-direction: column;
  justify-content: center;
  margin: 0;
  transition: transform .5s ease-in;
  display: flex;
  position: fixed;
  top: 10vh;
  right: 0;
  transform: translateX(100%);
}

.navigation__list--open {
  transform: translateX(0);
}

.navigation__item {
  padding: 20px 10px;
  list-style: none;
}

.navigation__item:nth-of-type(1) a {
  animation-delay: .3s;
}

.navigation__item:nth-of-type(2) a {
  animation-delay: .4s;
}

.navigation__item:nth-of-type(3) a {
  animation-delay: .6s;
}

.navigation__item:nth-of-type(4) a {
  animation-delay: .8s;
}

.navigation__item:nth-of-type(5) a {
  animation-delay: 1s;
}

.navigation__link {
  color: #f5f5f5;
  text-transform: uppercase;
  letter-spacing: 2px;
  opacity: 0;
  font-size: 12px;
  font-weight: 700;
  text-decoration: none;
  transition: all .2s;
}

.navigation__link:hover {
  color: #bf9660;
}

@media (min-width: 868px) {
  .navigation {
    height: 8vh;
  }

  .navigation__list {
    height: 10vh;
    width: 80%;
    will-change: transform, opacity;
    background-color: #0000;
    flex-direction: row;
    justify-content: flex-end;
    align-items: flex-end;
    padding-bottom: 30px;
    display: flex;
    position: relative;
    top: 0;
    transform: translateX(0%);
  }

  .navigation__item {
    justify-content: space-between;
    padding: 0 20px;
    display: flex;
  }

  .navigation__link {
    color: #302b2bf5;
    opacity: 1;
    font-size: 14px;
  }

  .navigation__link:active {
    color: #bf9660;
    transform: translateY(2px);
  }

  .navigation__hamburgerButton, .navigation__list-arrowButton {
    display: none;
  }
}

@media (min-width: 900px) {
  .navigation {
    padding: 0 20px;
  }
}

@media (min-width: 1024px) {
  .navigation__logoContainer {
    max-width: 150px;
  }

  .navigation__list {
    width: 100%;
  }
}

.observer-nav-background {
  background-color: var(--nav-scroll-observer);
  box-shadow: 0 3px 20px #0003;
}

.observer-nav-color {
  color: #f5f5f5;
}

.js-active .navigation__firstLine {
  transition-delay: .22s;
  transition-timing-function: cubic-bezier(.215, .61, .355, 1);
  transform: translate3d(0, -10px, 0)rotate(-45deg);
}

.js-active .navigation__firstLine:after {
  opacity: 0;
  transition: top .2s cubic-bezier(.33333, 0, .66667, .33333), opacity .1s linear .22s;
  top: 0;
}

.js-active .navigation__firstLine:before {
  transition: top .1s cubic-bezier(.33333, 0, .66667, .33333) .16s, transform .13s cubic-bezier(.215, .61, .355, 1) .25s;
  top: 0;
  transform: rotate(-90deg);
}

.js-active .navigation__firstLine {
  opacity: .7;
}

.linksFade {
  animation: linksFade .5s forwards;
}

@keyframes linksFade {
  from {
    opacity: 0;
    transition: translateX(50px);
  }

  to {
    opacity: 1;
    transform: translateX(0);
  }
}

.main-container {
  max-width: 1200px;
  background-color: var(--container-bgc);
  margin: 0 auto;
  padding: 0 30px 0 24px;
  transition: background-color .3s linear;
  position: relative;
}

.buttonToggle {
  width: 40px;
  height: 40px;
  background: var(--button-background);
  color: var(--button-color);
  cursor: pointer;
  z-index: 900;
  border: none;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  transition: all .5s;
  display: flex;
  position: absolute;
  top: 0;
  right: 20px;
}

.buttonToggle:focus {
  outline-offset: .5px;
  outline: 1px solid #bf9660;
}

.buttonToggle--show {
  opacity: .7;
  transform: translateY(100px);
}

.buttonToggle-icon {
  background-image: var(--button-icon);
  width: 30px;
  height: 30px;
  filter: var(--button-icon-filter);
  background-position: 50%;
  background-repeat: no-repeat;
  transition: filter .5s ease-in-out;
}

@media (min-width: 500px) {
  .buttonToggle {
    top: 27px;
    right: 27px;
  }
}

.article__img {
  width: 100%;
  max-width: 770px;
  z-index: -1;
}

.article > div {
  background-color: var(--article-bgc);
  transition: background-color .3s linear;
}

.article__text {
  max-width: 541px;
  text-align: center;
  color: var(--article-text);
  margin: auto;
  line-height: 1.5;
}

.article__heading {
  letter-spacing: 2px;
  text-transform: uppercase;
  padding: 50px 0 20px;
  font-weight: 300;
  line-height: 1.5;
}

.article__special {
  color: #bf9660;
}

.article__paragraph {
  padding-bottom: 10px;
}

.article__paragraph--marginBottom {
  margin-bottom: 50px;
}

@media (min-width: 540px) {
  .article {
    position: relative;
  }

  .article__img {
    margin-left: auto;
    padding-top: 120px;
    display: block;
  }

  .article__text {
    transition: background-color .3s linear;
  }

  .article__text--positioning {
    text-align: start;
    background-color: #f8f8f8;
    padding: 0 40px 0 96px;
    position: absolute;
    bottom: -200px;
    left: 0;
  }

  .article__img--margin {
    margin: 180px 0 200px auto;
  }
}

.pair-tracks {
  background-color: #bf9660;
}

.pair-tracks__track {
  width: 100%;
  padding: 20px;
}

.pair-tracks__track--marginTop {
  margin-top: 30px;
}

.pair-tracks__track-iframe {
  width: 100%;
  height: 166px;
  border-radius: 4px;
}

.pair-tracks__track-link {
  color: #2c2323;
  font-size: 14px;
  text-decoration: none;
}

@media (min-width: 768px) {
  .pair-tracks {
    justify-content: center;
    align-items: center;
    display: flex;
  }

  .pair-tracks__track--marginTop {
    margin: 0;
  }
}

.feedback-wrapper .feedback {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0;
  display: flex;
}

.feedback-wrapper .feedback__img {
  height: auto;
  width: 100%;
  max-width: 400px;
  display: block;
}

.feedback-wrapper .feedback__text {
  background-color: var(--feedback-bgc);
  color: var(--feedback-text);
  cursor: pointer;
  padding: 20px;
  line-height: 1.5;
}

.feedback-wrapper .feedback__paragraph {
  opacity: .5;
}

.feedback-wrapper .feedback__author {
  text-align: right;
}

.feedback-wrapper .feedback__author-link {
  letter-spacing: 1px;
  color: var(--feedback-author);
  cursor: pointer;
  border-bottom: 1.8px solid #b3898917;
  text-decoration: none;
  transition: all .2s ease-in;
}

.feedback-wrapper .feedback__author-link:hover {
  border-bottom: 1.8px solid var(--feedback-author);
}

@media (min-width: 440px) {
  .feedback-wrapper .feedback {
    position: relative;
  }

  .feedback-wrapper .feedback__img {
    margin-bottom: 200px;
  }

  .feedback-wrapper .feedback__text {
    max-width: 300px;
    text-align: start;
    opacity: .93;
    padding-top: 60px;
    line-height: 2;
    position: absolute;
    bottom: 100px;
    box-shadow: 3px 3px 7px 1px #00000026;
  }

  .feedback-wrapper .feedback__text--right {
    right: 0;
  }

  .feedback-wrapper .feedback__text--left {
    left: 0;
  }
}

@media (min-width: 738px) {
  .feedback-wrapper .feedback__heading {
    margin-top: 50px;
  }

  .feedback-wrapper .feedback__text {
    max-width: 400px;
  }
}

@media (min-width: 1024px) {
  .feedback-wrapper .feedback__text {
    max-width: 500px;
    transform-origin: 0;
    transition: transform .5s;
    overflow: hidden;
  }

  .feedback-wrapper .feedback__text:hover {
    transform: scale(1.1);
  }
}

.contact {
  background-color: #bf9660;
  flex-direction: column;
  justify-content: center;
  margin: auto;
  display: flex;
}

.contact__heading {
  font-size: 40px;
  position: relative;
}

.contact__heading:before {
  content: "";
  height: 37px;
  width: 37px;
  background-image: url("dots-bg.054a6e79.svg");
  display: block;
  position: absolute;
  top: -14px;
  left: -14px;
}

.contact__description {
  height: 90%;
  background-image: url("map.5e21484e.svg");
  background-position: 0;
  background-repeat: no-repeat;
  background-size: cover;
  padding-top: 5px;
  font-size: 20px;
}

.contact .form__list {
  flex-direction: column;
  justify-content: center;
  padding: 0;
  list-style: none;
  display: flex;
  position: relative;
}

.contact .form__item {
  margin-bottom: 14.5px;
}

.contact .form__item-label {
  margin-bottom: 8px;
  display: block;
}

.contact .form__item-field {
  width: 100%;
  height: 40px;
  border: 0;
  border-radius: 5px;
  padding: 23px 40px;
  box-shadow: 0 5px 20px #2f36401a;
}

.contact .form__item-field--last {
  resize: vertical;
  min-height: 200px;
  padding: 15px 40px;
  overflow: auto;
}

.contact .form__item-field::placeholder {
